<template>
    <div class="chat">
        <div class="thread" v-chat-scroll="{always: false, smooth: true}">
            <infinite-loading direction="top" @infinite="fetchMessages"></infinite-loading>
            <ChatMessage v-for="(message, id) in messages" :key="id" :message="message"></ChatMessage>
        </div>
        <textarea class="compose" type="text" placeholder="Enter message..." v-model="content" @keydown.enter.exact.prevent="sendMessage" :style="composeHeight"></textarea>
    </div>
</template>

<script>
    import ChatMessage from '@/components/Message'
    import API from '@/api'
    import InfiniteLoading from 'vue-infinite-loading';
    
    import EmojiConvertor from 'emoji-js'

    let emoji = new EmojiConvertor();
    emoji.text_mode = false;
    emoji.replace_mode = 'unified';

    export default {    
        name: 'Chat',
        components: {
            ChatMessage,
            InfiniteLoading
        },
        data() {
            return {
                content: '',
                loadingMessages: false
            }
        },
        methods: {
            sendMessage() {
                if (!this.content.length) return;
                API.sendMessage(this.activeChannel,emoji.replace_colons(this.content))
                this.content = '';
            },
            async fetchMessages($state) {
                if (!this.activeChannel) return setTimeout(() => $state.loaded(),1000);
                let messages = await API.fetchMessages(this.activeChannel,this.earliestMessage);
                messages.forEach(message => {
                    this.$store.commit('addToMessageCache', message);
                });
                this.$store.commit('sortMessageCache', this.activeChannel);
                $state.loaded();
            }
        },
        computed: {
            activeChannel() {
                return this.$store.state.activeChannel;
            },
            messages() {
                return this.$store.state.messageCache[this.$store.state.activeChannel] || {};
            },
            composeHeight() {
                let baseHeight = 30;
                let newLines = this.content.split('\n').length-1;
                if (newLines > 6) newLines = 6;
                let height = (baseHeight + ((newLines)*20));
                return {height: height + 'px'}
            },
            earliestMessage() {
                if (!this.activeChannel) return
                let cache = this.messages;
                let sorted = Object.values(cache).sort((a, b) => a.id - b.id);
                if (!sorted.length) return
                return sorted[0].id;
            }
        },
    }
</script>